import React from "react";
import styles from "./KeySpec.module.scss";

function getKeySpecs(product) {
  let detail = Object.keys(product)
    .filter((keyspec) => keyspec.startsWith("key spec"))
    .map((e) => product[e]);
  return detail;
}

const KeySpec = ({ product }) => {
  const { Disclaimer } = product;
  return (
    <>
      <ul className={styles.productDescription}>
        {getKeySpecs(product)
          .slice(0, 4)
          .map((spec, index) => {
            return spec !== "" ? <li key={index}>{spec}</li> : "";
          })}
      </ul>
      {Disclaimer && (
        <div className={styles.productDisclaimer}>
          <li>Disclaimer</li>
          <li>{Disclaimer}</li>
        </div>
      )}
    </>
  );
};

export { KeySpec };
