import React from "react";
import styles from "./IndicationComponent.module.scss";

const IndicationComponent = ({ Indication }) => {
  function getIndications(indication) {
    const ind = indication.split("\n");
    return ind;
  }
  return (
    <div>
      {Indication
        ? getIndications(Indication).map((ind, index) => {
            return ind.includes(":") ? (
              <table key={index}>
                <tbody>
                  <tr>
                    <td key={index} className={styles.indication}>
                      {ind.split(":")[0]}
                    </td>
                    <td className={styles.indication}>:</td>
                    <td className={styles.indication}>{ind.split(":")[1]}</td>
                  </tr>
                </tbody>
              </table>
            ) : (
              <li key={index} className={styles.indication}>
                {ind}
              </li>
            );
          })
        : null}
    </div>
  );
};

export { IndicationComponent };
