import React, { useEffect, useState } from "react";
import { ProductModal } from "../ProductModal/ProductModal";
import { VideoModal } from "../VideoModal/VideoModal";

const PRODUCTS = require("../../products.json");
const VARIATIONS = require("../../variations.json");

const findProduct = (jsonList, model, country) => {
  return jsonList.find((product) => product.Model === model && product.Site === country);
};

function ModalContainerComponent({ vistaContent }) {
  const [variant, setVariant] = useState(null);
  const [product, setProduct] = useState(null);

  function toggleVistaAudio(state) {
    const audios = vistaContent.current.contentWindow.currentGlobalAudios;
    for (let audio in audios) {
      if (state === "ON") {
        audios[audio].audio.play();
      }
      if (state === "OFF") {
        audios[audio].audio.pause();
      }
    }
  }

  // lives inside VideoPlayer component
  const [openVideo, setOpenVideo] = useState(false);
  function openVideoModal() {
    setOpenVideo(true);
    toggleVistaAudio("OFF");
  }
  function closeVideoModal() {
    setOpenVideo(false);
    toggleVistaAudio("ON");
  }

  const handleClose = () => {
    setVariant(null);
    setProduct(null);
    setOpenVideo(false);
    toggleVistaAudio("ON");
  };

  useEffect(() => {
    function callback(e) {
      if (e.data.model) {
        const targetProduct = findProduct(PRODUCTS, e.data.model, e.data.country);
        setProduct(targetProduct);
      }
    }
    window.addEventListener("message", callback);

    return () => {
      window.removeEventListener("message", callback);
    };
  }, []);

  return (
    <>
      {openVideo && <VideoModal closeVideoModal={closeVideoModal} selectedProduct={variant || product} />}
      <ProductModal
        handleClose={handleClose}
        openVideoModal={openVideoModal}
        selectedProduct={variant || product}
        setVariant={setVariant}
        variationOptions={findProduct(VARIATIONS, product?.Model, product?.Site)?.Variations || []}
      />
    </>
  );
}

const ModalContainer = React.memo(ModalContainerComponent);

export { ModalContainer, findProduct };
